@import url('https://www.w3schools.com/w3css/4/w3.css');
@import url('https://fonts.googleapis.com/css2?family=Space+Mono:ital,wght@0,400;0,700;1,400;1,700&display=swap');
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css');
*{
    letter-spacing: -1px;
}
body,
h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'Space Mono', monospace;
}

.w3-row-padding .w3-col {
    margin-bottom: 16px;
}

.bgimg {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 100%;
}
w3-content img.w3-image{
    border: 5px solid lightgray;
}
code{
    background-color:  #f0f0f1;
}
a.word-break, 
a.link{
    word-break: break-all;
}
a:hover{
    text-decoration: underline;
    font-weight: bold;
}
a.w3-button:hover{
    color: inherit;
    background: inherit;
}
.w3-wide {
    letter-spacing: 1px;
}
.w3-modal-content img{
    max-width: 100%;
}
ul li{
    text-align: left;
}

.item .w3-card{
    height: 100%;
}

/*CV page */
body.cv{
    background-color: #616161!important;
}
body.cv #cvcontent h2{
    position: relative;
}
body.cv #cvcontent .w3-twothird h2:before{
    background: url("https://quyle91.github.io/img/underline.png") 50% top no-repeat;
    background-size: 100% 9px;
    border-radius: 999px;
    color: green;
    content: "";
    height: 9px;
    image-rendering: -webkit-optimize-contrast;
    left: 0;
    position: absolute;
    right: 0;
    top: 80%;
    max-width: 200px;
}
body.page-404{
    display: flex;
    height: 100vh;
    justify-content: center;
    align-items: center;
    text-align: center;
}
@media (min-width:  601px){
    #cvcontent{
        display: flex;
        flex-direction: row-reverse;
    }
}
.mrr8{
    margin-right:  8px;
    margin-bottom: 8px;
}
/*Equal height col*/
.col-container{
    display: flex;
    flex-wrap: wrap;
}

.col-container .col-full{
    width: 100%;
}
.row-fix-margin{
    margin-left: -16px;
    margin-right: -16px;
}
.project-company{
    display: flex;
    align-items: center;
    gap: 8px;
    flex-wrap: wrap;
}
@media (min-width:  601px){
    .project-company{

    }
}

.project-company.sentius small{
    margin-top: 10px;
}
#nav-icon{
    z-index: 2;
}
#content-project .link{
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;  
    overflow: hidden;
}
@media (max-width: 600px){
    .project-title{
        font-size: 16px;
    }
}
a.link::after,
.content a::after{
    content: "\f08e";
    display: inline-block;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: 0.8em;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    margin-left: 6px;
    opacity: 0.4;
}
img{
    max-width: 100%;
}
.zthumbnail{
    aspect-ratio: 1/1;
    object-fit: cover;
}

blockquote{
    margin-left: 0;
    padding: 0.5em 0.5em 0.5em 1em;
    border-left: 5px solid lightgray;
    background-color: #f1f1f1!important;
    margin-right: 0px;
}

.post-content img{
    border: 1px solid lightgray;
    margin-bottom: 1em;
    height: auto;
}
@media (min-width: 1024px){
    .post-content img{
        max-width: 800px;

    }
}

@media (max-width: 600px) {
    .blog-item .right .w3-container{
        padding-top: 0px !important;
    }
    .blog-item .right .w3-container h4{
        margin-top: 0px;
        font-size: 1em;
    }
}
